.login_wrapper{
    width: 30%;
    margin:45px auto;
    padding: 35px;
    background-color: #435c70;
}

.login_wrapper h1{
    font-size: 18px;
    text-align: center;
    color: white;
}


.login_form label{
    display: block;
    font-size: 15px;
    padding-bottom: 15px;
    font-weight: 500;
    color: white;
}

.login_form{
    display:  flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 25px;
}

.login_form div{
    width: 100%;
}

.login_form input{
    padding: 15px  ;
    width: 100%;
    margin-bottom: 10px;
    background-color: #54657d;
    outline: none;
    border: none;
   
}

.login_btn{
    padding: 15px;
    width: 100%;
    background-color: #f5a623;
    margin-top:10px ;
    border: none;
    cursor: pointer;
    font-weight: 600;
    color: white;
}



@media screen and (max-width:830px){
    .login_wrapper{
        width: 50%;
    }
}

@media screen and (max-width:550px){
    .login_wrapper{
        width: 80%;
    }
}