.component{
    background-color: red;
    height: 425px;
    margin: 10px;
    padding: 35px;
    background-color:#435c70 ;
    color: white;
}
.component h1{
    font-size: 18px;
}


.dashboardGrid{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 1rem;
    margin-bottom: 28px;
}

.dashboard{
    margin: 30px 7%; 
    
}


.welcomeText{
    margin: 46px 13px;
    color: white;
    font-weight: 400;
    font-size: 17px;
}

.welcomeText span{
    font-weight: bold;
}

.notificationContainer{
    overflow-y: scroll;
    height: 300px;
    margin-top: 20px;
}



.pic img{
    width: 65px;
    height: 65px;
    border-radius: 50%;
}


.pic{ 
    display: inline-block;
    padding-right: 20px;
}

.notificationBox{
    display: flex;
    align-items: center;
    background-color: #4e657a;
    padding: 10px;
    margin-bottom: 9px;
    
}

.notificationBox:last-child{
    margin-bottom: 0px;
}

.msg p:last-child{
    font-size: 15px;
    color: #8e9bc3;
}



.table{
    width: 100%;
    margin-top: 20px;
}

.table tr{
    margin: 0.5px 0px;
    background-color:#4e657a;
    
}

.dataRow td{
    display: inline;
}


.table th , td{
    padding: 15px 25px;
}

.table td{
    font-size: 15px;
}


.orderBox{
    overflow-y: scroll;
}

table tr .indicator{
    height: 5px;
    width: 5px;
    border-radius: 50%;
    margin-right: 10px;
}

table tr .red{
    background-color: red;
    box-shadow: 2px 2px 10px rgb(202, 88, 88) ,-2px -2px 10px rgb(201, 89, 89) ;
}

table tr .yellow{
    background-color: yellow;
    box-shadow: 2px 2px 10px lightyellow ,-2px -2px 10px lightyellow
}

table tr .green{
    background-color: rgb(14, 231, 14);
    box-shadow: 2px 2px 10px lightgreen ,-2px -2px 10px lightgreen
}

.table tr .darkGreen{
    background-color: darkgreen;
    box-shadow: 2px 2px 10px green ,-2px -2px 10px green
}




.stausColumn{
    display: flex;
    align-items: center;
}

.storage{
    display: flex;
    justify-content: center;
    height: 85%;
    margin-top: 20px;
    width: auto;
} 

.chart_box{
    display: flex;
    align-items: center;

    margin: auto;
    width: 80%;
    height: 90%;
}




@media screen and (max-width :450px) {
    .component{
        padding: 0px;
        margin: 0px;
    }

    .storage{
        width: 70%;
        height: 70%;
    }
    .notificationContainer{
        height: 182px;
    }
    .component{
        height: 294px; ;
    }
}


@media screen and (max-width:1010px){
    .dashboardGrid{
        display: grid;
        grid-template-columns: repeat(1,1fr);
        grid-gap: 1rem;
    }
}








