.navbar{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0px 100px;
    background-color: #567086;
    color: white;
    height: 100px;
}

.menu{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 30%;
    width: 84%;
    transition: height 0.5s;
}


.menu li{
    list-style: none;
    
    display: flex;
    align-items: center;
    flex-direction: column;
   padding: 23px;
   margin-right: 10px;
    cursor: pointer;
    transition: all 0.4s;
}


.items{
    display: flex;
}

.menu li svg{
    font-size: 28px;
    margin-bottom: 5px;
    transition: all 0.3s;
}

.menu li:hover svg {
    color:#f5a623 ;
}


.menu a:hover {
    color: #f5a623;
}

.logout{
    font-weight: 500;
    cursor: pointer;
    padding: 15px;
    transition: all 0.4s;
}
.logout span{
    font-weight: 300;
}

.logout:hover{
    color: #f5a623;
}

.logo{
    font-size: 20px;
    cursor: pointer;
    width: 43%;
}

.burger{
    padding: 10px;
    cursor: pointer;
    box-shadow: 1px 1px 4px rgb(145, 143, 143) ,
     -1px -1px 1px rgb(145, 143, 143);
    display: none;

}

.line{
    background-color: white;
    height: 3px;
    width: 23px;
    margin: 5px 0px;
}

.active{
    background-color: #f5a623;
}
.active:hover li{
    color: white;
}

.active:hover li svg{
    color: white;
}


@media screen and (max-width:1050px){
    .navbar{
        padding:  0px 50px;
    }
}


@media screen and (max-width:850px){
    .menu{
        display:block;
        height: 0px; 
        overflow: hidden;
        position: absolute;
        top: 75px;
        right: 100px;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        width: auto;
        background-color: #567086;
        
    }
    

    .res_menu{
        display:flex;
        height: 200px;
        position: absolute;
        top: 75px;
        right: 100px;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        width: auto;
        background-color: #567086;
        transition: height 0.5s;
        overflow: hidden;
    }

    .menu li{
        flex-direction: row;
        margin-right: 0px;
        padding: 10px 50px;
    }
    
    .user{
        padding: 15px;
    }


    .menu li svg{
        margin-right: 10px;
        font-size: 22px;
    }

    .items{
        flex-direction: column;
    }

    .burger{
        display: block;
        position: relative;
    }
    .navbar{
        padding:  0px 100px;
    }
}

@media screen and (max-width:600px){

    .navbar{
        padding:  0px 20px;
    }
    .menu{
        right: 20px;
    }
    .logo{
        width: 100%;
    }
}

