.product_container{
    display:  flex;
    justify-content: center;
    padding: 40px 0px;
}

.details{
    background-color: #435c70;
    width: 47%;
    height: 100vh;
    margin-right: 30px;
    padding: 25px;
}

.category{
    background-color: #435c70;
    width: 20%;
    height: 425px;
}

.details_box{
    display:  flex;
    justify-content:center ;
    height: 435px;
    overflow-y: scroll;
}
.details_box table{
    width: 100%;
}



.table_head{
    background-color:#486177;
     color: white;
    width: 100%;
    padding: 10px;
}

.table_head th{
    font-size: 12px;
    margin: 0px 10px;
    padding: 11px  0px;
}

.table_data{
    background-color: #50697f;
    color: white;
    width: 100%;
}

.table_data td{
    font-size: 14px;
    padding: 10px 30px;
}


.delete{
    display: inline-block;
    background-color: #394e64;
    padding: 5px;
    border-radius: 50%;
    font-size: 18px;
    margin-left: 20px;
    cursor: pointer;
}

.checkbox{
    margin-right: 10px;
    cursor: pointer;
}



.table_data td:last-child{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 20px;

}

.categor_title{
    font-size: 16px;
    color: white;
}

.category{
    padding: 25px;
}

.category_box{
        height: 260px;
        overflow-y: scroll;
        overflow-x: hidden;
}

.category_card{
    background-color: #50697f;
    color: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 1px;
    width: 100%;
    padding: 10px 8px;
}

.categor_title{
    margin-bottom: 20px;
}

.category_card h4{
    font-size: 14px;
}

.button_wrapper{
    width: 100%;
    padding: 15px;
    margin-top: 15px;
    background-color: #f5a623;
    color: white;
    border: none;
    cursor: pointer;
}

.addproduct_container{
    
    height: 80vh;
    width: 60%;
    display: block;
    margin: 30px auto;
    background-color: #435c70;
    color: white;
    padding:30px 38px;
}

.addproduct_container h2{
    font-size: 16px;
    margin-bottom: 20px;
}

.d_flex{
    display: flex;
    justify-content: space-between;
}

.row{
    display: flex;
}

.form_section label{
    display: block;
    padding: 8px 0px;
}

.form_section input,textarea,select{
    width: 100%;
    padding: 11px;
    outline: none;
    background-color:#50697f;
    border: none;
}

.form_section{
    width: 50%;
    margin-right: 10px;
}


.img_upload_section{
    width: 50%;
    margin-left: 10px;
}

.img_upload_section .img{
    background-color: rgb(196, 192, 192);
    width: 100%;
    height: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: black;
    font-size: 40px;
}

.a_btn{
    width: 100%;
    padding: 12px;
    color: white;
    background-color: #f5a623;
    border: none;
    cursor: pointer;
    margin-top: 12px;
}




@media screen and (max-width:1050px){
    .product_container{
        flex-direction: column;
        align-items: center;
    }

    .details{
        width: 70%;
    }

    .category{
        width: 50%;
        margin-top: 40px;
    }
}

@media screen and (max-width:800px){
    .table_head th{
        font-size: 9px;
        margin: 0px 10px;
        padding: 11px  0px;
    }
    
    .table_data{
        background-color: #50697f;
        color: white;
        width: 100%;
    }
    
    .table_data td{
        font-size: 10px;
        padding: 10px 30px;
    }

    .button_wrapper{
        font-size: 8px;
    }

    .category_box{
        font-size: 10px;
    }
}

@media screen and (max-width:375px){
    .details{
        width: 90%;
    }


}



@media screen and (max-width:1000px){
    .d_flex{
        flex-direction: column;
        align-items: center;
    }
    .addproduct_container{
        height: auto;
    }

    .form_section {
        width:95%;
    }

    .form_section input[type = 'date']{
        width: 100%;
    }

    .img_upload_section{
        margin-top: 30px;
    }
}

@media screen and (max-width:484px){
    .addproduct_container{
        width: 85%;
    }

    .img_upload_section{
        width: 70%;
    }
}