.account_container{
    height: fit-content;
    width: 75%;
    margin: 40px auto;
}


.account_list{
    background-color: #435c70;
    height: auto;
    color: white;
    padding: 30px;
}
.account_list h3{
    font-size: 16px;
    padding-bottom: 20px;
}
.account_list p{
    font-size: 15px;
    padding-bottom: 12px;
}


.account_setting_wrapper{
    display: flex;
    margin-top: 25px;
}

.avatar{
    background-color: #435c70;
    color: white;
    width: 35%;
    height: 375px;
    margin-right: 25px;
    padding: 30px;
}

.avatar h3{
    font-size: 16px;
    margin-bottom: 10px;
}

.avatar img{
    width: 100%;
    height: 72%;
}

.setting{
    background-color: #435c70;
    color: white;
    width:65% ;
    height: auto;
    padding: 30px;
}

.setting h3{
    font-size:16px ;
    padding-bottom: 20px;
}

.setting .form label{
    display: block;
    font-size: 14px;
    margin-bottom: 10px;
}

.detete_btn{
    width: 100%;
    padding: 12px;
    margin-top: 20px;
    color: white;
    background-color: #f5a623;
    border: none;
    cursor: pointer;
}

.account_select{
    width: 100%;
    padding: 13px;
    outline: none;
    background-color: #50657b;
    border: none;
    color: #88acc7;
}

.form{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 1.1rem;
}


.form input{
    width: 100%;
    padding: 12px;
    background-color: #50657b;
    border: none;
    outline: none;
}

.grid button{
    padding: 12px;
    width: 100%;
    background-color: #f5a623;
    color: white;
    border: none;
    cursor: pointer;
}

.form :last-child{
    display: flex;
    align-items: flex-end;
    justify-content: center;
}


@media screen and (max-width:800px){
    .account_setting_wrapper{
        flex-direction: column;
        align-items: center;
    }

    .avatar{
        width: 70%;
    }

    .setting{
        margin-top: 40px;
        width: 90%;
        padding: 25px;
    }
    .account_container{
        margin: 10px auto;
        width: 90%;
    }
    .detete_btn{
        font-size: 10px;
    }
    .grid button{
        font-size: 10px;
    }
}